@import "src/index.scss";

footer {
  background-color: #101012;
  padding: 7rem 0 5rem 0;
}

.footer {
  display: flex;
  justify-content: space-around;
  align-items: flex-start;
  max-width: 144rem;
  padding: 1.5rem;
  margin: 0 auto;

  &-links {
    list-style: none;
    padding-left: 0;
    margin: 0;
  }

  &-link {
    margin-bottom: 1.8rem;

    &--item {
      font-size: 1.8rem;
      color: #C2C2C2;
      text-decoration: none;
      font-weight: 600;
      transition: .2s ease color;

      &:hover {
        color: $white;
      }
    }
  }
}